import { Component, OnInit } from '@angular/core';
import { SWeatherService } from '../../services/s-weather.service';

@Component({
  selector: 'app-weather-app',
  templateUrl: './weather-app.component.html',
  styleUrls: ['./weather-app.component.scss'],
})
export class WeatherAppComponent implements OnInit {
  public weather: any;
  constructor(private data: SWeatherService) {}
  /**
   * getWeather
   */
  public getWeather(cityName: string, CountryCode: string) {
    this.data.getWeather(cityName, CountryCode).subscribe((res: any) => {
      console.log(res);
      this.weather = res;
    });
  }

  /**
   * submitLocation
   */
  public submitLocation(
    cityName: HTMLInputElement,
    CountryCode: HTMLInputElement
  ): boolean {
    console.log(cityName.value, CountryCode);
    this.getWeather(cityName.value, CountryCode.value);
    cityName.value = '';
    CountryCode.value = '';
    cityName.focus();
    return false;
  }
  public ngOnInit(): void {}
}
